<template>
  <div>
    <div class="cms-list-layout">
      <cms-prompt>
        <li>显示卡密模板信息,可对模板进行新增、编辑、删除及添加数据操作</li>
      </cms-prompt>
      <section class="cms-list-query">
        <div class="query-group">
          <label>模板名称</label>
          <el-input v-model="queryParams.name"></el-input>
        </div>
        <div class="query-group">
           <label>使用量</label>
          <el-input v-model="queryParams.minUsed"></el-input><span> ~ </span>
          <el-input v-model="queryParams.maxUsed"></el-input>
          <cms-button size="small" @click="query">查询</cms-button>
        </div>
      </section>
      <section class="cms-batch">
        <cms-button icon="add" type="success" size="small"  @click="$routerLink('/product/virtualCard/save'),'save'">新增</cms-button>
        <cms-button icon="error" type="error"  size="small"   @click="deleteDatas(ids)">批量删除</cms-button>
      </section>
      <el-table :data="tableData" style="width: 100%" stripe ref='dataTable' v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows'>
      <el-table-column type="selection" width="56" fixed='left' ></el-table-column>
      <el-table-column prop="id" label="id" width="200" align="center"></el-table-column>
      <el-table-column prop="" label="模板名称" width="200" align="center">
        <template slot-scope="scope">
          <div class="text-overflow">{{scope.row.name}}</div>
        </template>
      </el-table-column>
      <el-table-column  label="已使用/总共数据" min-width="440" align="center">
        <template slot-scope="scope">
          <span>{{scope.row.useNumber+'/'+scope.row.total}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width='305' fixed="right" align="center">
        <template slot-scope="scope">
          <el-button icon='edit' size='small' type='text' @click="$routerLink('/product/virtualCard/showUse','showUse',{id:scope.row.id})">查看使用情况</el-button>
          <el-button icon='edit' size='small' type='text' @click="$routerLink('/product/virtualCard/dataSave','dataSave',{id:scope.row.id})">添加数据</el-button>
          <el-button icon='error' size='small' type='text' @click="$routerLink('/product/virtualCard/edit','edit',{id:scope.row.id})">编辑</el-button>
          <el-button icon='error' size='small' type='text'  @click.stop="deleteData(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
    </div>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { productVirtualCardtpl, productVirtualCardtplDelete } from '@/api/product/product.js'
var defaultData = {
  'page': '',
  'size': '',
  'name': '',
  'maxUsed': '',
  'minUsed': '',
  'storeId': ''
}
export default {
  mixins: [list],
  data() {
    return {
      multipleSelection: [],
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(productVirtualCardtplDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas(ids) {
      this.deleteTableDatas(productVirtualCardtplDelete, ids) // 这个才是真实的删除方法
    }

  },
  created() {
    this.getTableData(productVirtualCardtpl, this.queryParams)
  }
}
</script>
<style>
</style>





